*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
}


li{
    list-style-type: none;
    display: flex;
    flex-direction: column;
    margin-top: 30px;
   
}
.container{
    display: flex;
    
    height: 70vh;
}
.data{
    margin-top: 26px;
}
.formcontainer {
    display: flex;
    flex-direction: column;
    align-items: center;
 
  }
.inp{
    margin-top: 16px;
    padding: 12px;
    font-size: 16px;
    border: none;
    border-radius: 6px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}
.btn{
    padding: 12px;
    border-radius: 6px;
    margin-left: 8px;
    border: none;
    background: linear-gradient(135deg, #ff7e5f, #feb47b);
}
.btn:hover{
    background: linear-gradient(135deg, #ff9770, #fecc8c);
}
.heading{
    background-image: linear-gradient(135deg, #ff7e5f, #feb47b);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.moivecontainer{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    flex-wrap: wrap;
    
}
.card{
    width: 200px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3); 
  border-radius: 10px; 
  background: linear-gradient(135deg, #1a1a1a, #333333);
}
.card img{
    width: 200px;
    height: 200px;
    
}
.carddata{
    width: 200px;
    background: linear-gradient(135deg, #1a1a1a, #333333);
    padding: 8px;
    border-bottom-left-radius: 6px;
    border-bottom-right-radius: 6px;
}
.carddata p{
    margin: 4px 0;
}
.title{
    font-size: 16px;
    color:#FFFFFF;
}
.info{
    font-size: 12px;
    color: #CCCCCC;
}
span{
    font-weight: bold;
}
.mname{
    color:#800080;
}
.minfo{
    color: gold;
}
.loader{
    margin-top: 10px;
    color: darkblue;
}

@media only screen and (max-width: 600px) {
    .formcontainer {
      width: 90%; 
    }
    .inp{
        width: 75%;
    }
    .btn{
        width: 20%;
    }
    /* .heading{
        display: none;
    } */
    .container{
        display: flex;
        flex-direction: column;
        margin: 16px 0;
    }
    .card{
        margin-top: 15px;
    }
